*{
    margin: 0px auto;
    height: 0px auto;
  }
  
  body{
  width:100%;
  min-height: 100% ;
  background-image: url(/src/background-image/Chicago.jpg);
  background-size: cover;
  }
  
  footer
{
    width: 100%;
   position: fixed;
   clear: both;
    bottom: 0;
    left: 0 ;
    height: 30px;
    line-height: 30px;
    background: rgb(43, 42, 42);
    opacity: 0.8;
    color: #E7E7EB;
    text-align: center;
    font-family: 'Days One', sans-serif;
}

  
  @media (max-width: 1024px){
  
    body {
        background-image: url(/src/background-image/Chicago2.jpg);
    }
    footer{
      font-size: 15px;
  }
  
  @media (max-width: 768px){
  
    body {
        background-image: url(/src/background-image/Chicago3.jpg);
    }
    footer{
      font-size: 13px;
  }
  }
  
  @media (max-width: 595px){
  
    body {
        background-image: url(/src/background-image/movil.jpg);
        background-size: cover ;
    }
    h1{
        color:rgb(216, 211, 211);
    }
  }
  
  @media (max-width:320px){
    footer{
    font-size: 12px;}
  }
}