
h1
{
  font-size: 20px;
  margin-left: 20px;
  margin-top: 20px;
  font-family: 'Days One', sans-serif ;
  font-weight: lighter;
  text-shadow: 2px 4px 2px rgba(0,0,0,0.6);
  color:rgb(216, 211, 211);
  
}
h1 i{
  font-size: 20px;
  margin-left: 5px;
  color:rgb(216, 211, 211);
 
}
.div-container
{
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 6.25rem;
  width: auto;
  height: 6.25rem;
  justify-content: center;
}
.form
{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin: 0px auto;
}
.contain-input
{
  border: 1px solid #E7E7EB;
  height: 38px;
  border-radius: 5px;
  background-color: rgb(39, 39, 39);
  opacity: 0.6;
}


#nameCity
{
  background-color: transparent;
  outline: none;
  border: none;
  line-height: 38px;
  color: snow;
  font-size: 15px;
}
#nameCity::placeholder
{ color: white;
  font-size: 14px;
}

#icono
{
  color:  #78797a;
  margin-left: 5px;
}

#button-search
{
  width: 70px;
  height: 38px;
  padding: 6px;
  font-size: 14px;
  margin-left: 10px;
  background-color: rgb(17, 180, 245);
  border: green;
  border-radius: 5px;
  color:snow;
  cursor:pointer;
}

#button-search:hover
{
  background: rgb(45, 188, 245);
}

/*  componente infoweather          */


#today
{
  display: none;
  font-family: "roboto";
  text-align: center;
  color: rgb(0, 0, 0);
  width: 300px;
  height: 300px;
  background:rgb(211, 209, 209);
  justify-content: center;
  border: 1px solid #E7E7EB;
  border-radius: 5px;
  opacity:0.8;
  
}

@keyframes fadein{
  0%{opacity: 0 ;}
  100%{opacity:0.8;}
}




.temp
{
 font-size: 30px;
 font-family: Arial, Helvetica, sans-serif;
 font-weight: bold;
}
.description
{
 
  margin-top: 10px;
  font-size: 20px;
  
  
  
}
.city
{
  font-size: 20px;
  font-weight: bold;
  margin-top: 20px;
}
.details
{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 5px;
  font-family:"roboto";
  font-size: 14px;
  border-top: 1px solid gray;
  margin: 10px;
  padding-top: 10px;
}

.humidity{
  margin-left: 8px;
}

.windspeed
{
  margin-left: 7px;
}





#location-icon
{
  margin-right: 10px;
}



/**/



@media (max-width:320px){
.div-container{
  margin-top: 50px;
  width: 280px;
}
.today{
  width: 267px;
}

#nameCity{
  width: 160px;
}
}


